import React, {useState} from 'react'
import styled from 'styled-components'
import { PageLayout } from '@chac/common/style/sytles'
import {useCustomModal} from '@hooks/useModal'
import {useNavigate} from 'react-router-dom'
import {useForm} from 'react-hook-form'
import { useAlert } from '../hooks/useModal'
import { dummyApi, useCreatePetInfoMutation, useGetPetListQuery, useGetSearchPetQuery } from '../query/dummy'
const StyledPageLayout = styled(PageLayout)`
	.title {
		font-weight: 500;
		text-align: left;
	}
	h1.title {
		font-size: 1.8rem;
		margin: 20px 0;
	}
	h3.title {
		font-size: 1.2rem;
	}
	h4.title {
		font-size: 1rem;
		
	}
	section {
		margin-top: 20px;
	}
	small.error-text {
		display: block;
		color: red;
		font-size: 1rem;
		text-align: left;
		padding-left: 14px;
	}
`

const StyledInput = styled.div`
	text-align: left;
    padding: 15px;
    border: solid 1px #e2e2e2;
    border-radius: 10px;
	margin: 10px 0;
	border-color: ${props => props['aria-invalid'] ? 'red' : '#e2e2e2'};
	background-color: ${props => props.disabled ? '#e2e2e2': '#ffffff' };
	label {
		display: block;
		font-size: 0.9rem;
		font-weight: 400;
		margin-bottom: 8px;
	}
	.input-box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		input {
			font-size: 1.2rem;
			font-weight: 400;
			width: auto;
			flex: 1 auto;
		}
	}
`
const RadioGroup = styled.fieldset`
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 3px;
`
const StyledRadioItem = styled.div`
 	padding: 15px 10px;
	width: 50%;
	input[type=radio] {
		display: none;
	}
	label {
		display: inline-block;
		cursor: pointer;
		width: 100%;
		border: 1px solid #e2e2e2;
		border-radius: 10px;
		text-align: center;
		font-weight: 400;
		padding: 13px 10px;
    	font-size: 1.1rem;
	}
	label:has(input[type=radio]:checked){
		border: 1px solid blue;
		color: blue;
	}
`
const RadioItem = ({ readOnly, value, register, children}) => {
	return (
		<StyledRadioItem >
			<label>
				<input type='radio' value={value} {...register}/>
			 	{ children }
			</label>
		</StyledRadioItem>
	)
}
const StyeldSubmitButton = styled.button`
	width: 100%;
	background-color: ${props => props.isValid ? 'blue' : '#e2e2e2'};
	color: white;
	padding: 15px 10px;
	border-radius: 10px;
	margin: 10px;
`
const StyledButton = styled.button`
	background-color: #e2e2e2;
	padding: 10px;
	border-radius: 5px;
`
const StyledImage = styled.div`
	margin: 40px;
	.sample-image{
		background-color: aqua;
		width: 152px;
		height: 152px;
		border-radius: 50%;
		margin: 0 auto;
		font-size: 4rem;
		color: red;
		
	}
`
function Input(props) {
	const {type, label, name, readOnly, placeholder, inputChildren, register} = props;
	return (
		<StyledInput aria-invalid={props["aria-invalid"]} disabled={register.disabled}> 
			<div className='label-box'>
				<label htmlFor={name}>{label}</label>
			</div>
			<div className="input-box">
				<input type={type} readOnly={readOnly} placeholder={placeholder} {...register}/>
				{inputChildren}
			</div>
		</StyledInput>
	)
}
function ErrorMessage({errorObj}) {
	return errorObj && (<small className="error-text">{errorObj.message}</small>)
}
function Image(props) {
	return(
		<StyledImage>
			{ 
				props.imageSrc ? 
				<img src={props.imageSrc} onClick={props.onClick} /> :
				<div className='sample-image' onClick={props.onClick}>image</div> 
			}
		</StyledImage>
	)
}


export default function PetRegister() {
	const navigate = useNavigate()
	const [openPopup] = useCustomModal();
	const [openAlert] = useAlert();

	const [image, setImage] = useState(null);
	const [selectedPetKind, setSelectedPetKind] = useState(null);	// 선택한 펫 종류 data
	const [selectedPetColor, setSelectedPetColor] = useState(null);	// 선택한 펫 컬러 data

	const {data: searchPetData, isFetching} = useGetSearchPetQuery('DOG');
	const {data: petList, isFetching:isPetFetching} = useGetPetListQuery();
	const [runCheckAnimal, checkAnimalResult, lastPromiseInfo] =	dummyApi.useLazyCheckAnimalNumberQuery();
	const [runCreatePetInfo, {isLoading, isError}] = useCreatePetInfoMutation();
	if (!isPetFetching) {
		console.log(petList)
	}
	// 초기값
	// - 팻정보 수정을 통해 들어올 경우, 해당 펫 정보로 세팅해주면 됨.
	const initValues = {
		isRegisterNo: 'Y'
	}
	const {
		register, 
		handleSubmit,
		getValues,
		setValue,
		setFocus,
		setError,
		watch,
		formState: { isSubmitting, isDirty, isValid, errors },
	}  = useForm({
		mode: 'onChange',
		defaultValues: initValues
	});
	
	// react-form-hook 정합성 체크를 위한 데이터
	const registerOptions = {
		name: {
			required: '이름을 입력해주세요',
			minLength: {
				value: 2,
				message: '최소 2글자 이상 입력해주세요'
			}
		},
		kind: {
			required: '품종을 선택해주세요'
		},
		color: {
			required: '색상을 선택해주세요',
			disabled: !getValues('kind')
		},
		weight: {
			required: '몸무게를 입력해주세요',
			max: {
				value: 99,
				message: '몸무게를 다시 확인해주세요.'
			},
			maxLength: {
				value: 3,
				message: '몸무게를 다시 확인해주세요122.'
			},
			onChange: (e) => onChangeInput(e, 'weight'),
		},
		gender: {
			required: '성별을 선택해주세요',
		},
		isDesex: {
			required: '중성화 여부를 선택해주세요',
		},
		birthDate: {
			required: '생년월일을 입력해주세요',
			pattern: {
				value: /^\d{4}(0[1-9]|1[012])(0[1-9]|[12][0-9]|3[01])$/,
				message: '생년월일을 확인해주세요'
			},
			onChange: (e) => onChangeInput(e, 'date'),
		},
		adoptDate: {
			required: '입양일을 입력해주세요',
			pattern: {
				value: /^\d{4}(0[1-9]|1[012])(0[1-9]|[12][0-9]|3[01])$/,
				message: '입양일을 확인해주세요'
			},
			onChange: (e) => onChangeInput(e, 'date'),
		},
		isRegisterNo: {
			required: '동물등록번호 여부를 선택해주세요',
		},
		masterName: {
			required: '반려인의 이름을 입력해주세요',
			minLength: {
				value: 2,
				message: '최소 2글자 이상 입력해주세요'
			},
			disabled: (watch('isRegisterNo') == 'N')
		},
		registerNo: {
			required: '동물등록번호를 입력해주세요',
			pattern: {
				value: /^\d{15}$/,
				message: '동물등록번호 15자리를 정확히 입력해주세요'
			},
			onChange: (e) => onChangeInput(e, 'registerNo'),
			disabled: (watch('isRegisterNo') == 'N'),
		},
		isPetInsurance: {
			required: '펫보험 가입 여부를 선택해주세요',
		}
	}
	// 펫 종류 검색 팝업
	const btnSearchKindClick = () => openPopup({
		path: 'SearchPetKindPopup',
		props: searchPetData,
		closeHandler: function(result) {
			console.log(result);
			setSelectedPetKind(result);
			setValue('kind', result.petNm, {
				shouldValidate: true
			})
		}
	})
	// 펫 색상 검색 팝업
	const btnSearchColorClick = () => openPopup({
		path: 'SearchPetColorPopup',
		props: selectedPetKind?.colours,
		closeHandler: function(result) {
			console.log(result);
			setSelectedPetColor(result);
			setValue('color', result.colourNm, {
				shouldValidate: true
			})
		}
		
	})
	// 펫 프로필 이미지 선택 팝업
	const btnSelectProfileClick = () => openPopup({
		path: 'SelectProfileBottomSheet',
		closeHandler: function(result) {
			if (result == 'cancel') {
				return;
			}
			setImage(result.image);
		}
	})

	const onChangeInput = (e, type) => {
		const value = e.target.value;
		let condition = false;
		switch (type) {
			case "date": 
				condition = (!Number(value) || value.length > 8); 	break;
			case "weight":
				condition = (!Number(value) || value.length > 3); 	break;
			case "registerNo":
				condition = (!Number(value) || value.length > 15); 	break;
			default:
				break;
		}

		if (condition) {
			e.target.value = value.slice(0, -1);
		}
	}
	// form 정합성 통과 시 로직,
	const onSubmitHandler = async (result) => {
		/**
		 * todo
		 * 1. 기본 정합성 통과 시, 동물등록번호가 Y인 경우 "동물등록번호 조회 API" 결과에 따라 추가정합성 체크
		 * 		- if) 결과가 정합성 오류인 경우, setError 함수를 통해 동물등록번호에 에러표시 띄워준다.
		 * 2. 동물등록번호 까지 문제가 없다면, 서버에 등록 후, 등록된 동물정보로 메인화면 이동
		 * 3. 프로필 image 데이터는 app에 저장
		*/
		// 팻 종류/색상은 코드로 보내야 하지 않을까???
		const petKindCode = selectedPetKind.petCd;
		const petColorCode = selectedPetColor.colourCd;

		result.kind = petKindCode;
		result.color = petColorCode;
		try {
			if (result.isRegisterNo === 'Y') {
				const animalCheckResult = await runCheckAnimal({
					masterName: result.masterName,
					animalNumber: result.registerNo
				}).unwrap()
				console.log('animalCheckResult', animalCheckResult)
				console.log( checkAnimalResult)
				if (animalCheckResult.result === false) {
					// 에러 발생
					setError('registerNo',{message: '존재하지 않는 동물등록번호 입니다'})
					return false;
				}
			};

			const createResult = await runCreatePetInfo(result);
			console.log(createResult)
			if (createResult) {
				openAlert({
					title: "정합성 통과 & 서버 등록 데이터",
					content: "메인 화면으로 이동힙니다",
					closeHandler: function(result) {
						// navigate('/');
					}
				})
			}

		} catch (error) {
			
		}

		return;
	}
	// 에러 핸들러
	const onErrorHandler = (errors) => {
		// 1. 이미지 등록 여부 체크
		if (!image) {
			openAlert({
				content: '펫 이미지를 선택해주세요',
				closeHandler: () => {
					window.scrollTo({top: 0});
				}
			})
			return false;
		}
		// 2. 라디오 버튼 관련 정합성 오류는, alert으로 띄워준다.
		const radioNameList = ['gender', 'isDesex', 'isRegisterNo', 'isPetInsurance'];
		radioNameList.some(name => {
			if (errors[name]) {
				openAlert({
					content: errors[name].message,
					closeHandler: () => {
						// todo: setFocus했지만, input아닌 radio로는 포커스 전환이 안먹는 현상.. 수정필요
						setFocus(name, {shouldSelect: true})
					}
				})
			}
			return errors[name]
		})
	}

	if (isFetching) return (<h1>로딩중..</h1>);

	return (
		<StyledPageLayout>
			<h1 className='title'>
				건강체크에 필요한 정보를<br /> 
				입력해주세요
			</h1>
			<Image onClick={btnSelectProfileClick} imageSrc={image}></Image>
			<form onSubmit={handleSubmit(onSubmitHandler, onErrorHandler)} >
				<section>
					<h3 className="title">기본정보</h3>
					<Input type="text" label="반려동물 이름" placeholder="이름을 입력하세요" 
						aria-invalid={errors.name}
						register={register('name', registerOptions.name)} />
					<ErrorMessage errorObj={errors.name}/>

					<Input type="text" label="품종" placeholder="선택" readOnly 
						aria-invalid={errors.kind}
						inputChildren={(
							<StyledButton type="button" onClick={btnSearchKindClick}>품종찾기</StyledButton>
						)} 
						register={register('kind', registerOptions.kind)} />
					<ErrorMessage errorObj={errors.kind}/>
					
					<Input type="text" label="털색상" placeholder="선택" readOnly 
						aria-invalid={errors.color}
						inputChildren={(
							<StyledButton type="button" onClick={btnSearchColorClick}>V</StyledButton>
						)}
						register={register('color', registerOptions.color)} />
					<ErrorMessage errorObj={errors.color}/>

					<Input type="tel" name="weight" label="몸무게" placeholder="숫자" 
						aria-invalid={errors.weight}
						inputChildren={(
							<h3 className='title'>kg</h3>
						)}
						register={register('weight', registerOptions.weight)} />
					<ErrorMessage errorObj={errors.weight} />
				</section>
				<section>
					<h4 className="title">성별</h4>
					<RadioGroup>
						<RadioItem value="m" 
							register={register('gender', registerOptions.gender)} > 남자 </RadioItem>
						<RadioItem value="w" 
							register={register('gender', registerOptions.gender)} > 여자 </RadioItem>
					</RadioGroup>
				</section>
				<section>
					<h4 className="title">중성화</h4>
					<RadioGroup>
						<RadioItem value="Y" 
							register={register('isDesex', registerOptions.isDesex)} > 했어요 </RadioItem>
						<RadioItem value="N" 
							register={register('isDesex', registerOptions.isDesex)} > 안했어요 </RadioItem>
					</RadioGroup>
				</section>
				<section>
					<h4 className="title">기념일</h4>
					<Input type="tel" label="생년월일" placeholder="20210101" 
						aria-invalid={errors.birthDate}
						register={register('birthDate',registerOptions.birthDate)} />
					<ErrorMessage errorObj={errors.birthDate}/>

					<Input type="tel" label="입양일" placeholder="20210101" 
						aria-invalid={errors.adoptDate}
						register={register('adoptDate', registerOptions.adoptDate)} />
					<ErrorMessage errorObj={errors.adoptDate}/>
				</section>
				<section>
					<h4 className="title">동물등록번호</h4>
					<RadioGroup>
						<RadioItem value="Y" 
							register={register('isRegisterNo', registerOptions.isRegisterNo)} > 있어요 </RadioItem>
						<RadioItem value="N" 
							register={register('isRegisterNo', registerOptions.isRegisterNo)} > 없어요 </RadioItem>
					</RadioGroup>

					<Input type="text" label="등록 반려인명" placeholder="홍길동" 
						aria-invalid={errors.masterName}
						register={register('masterName', registerOptions.masterName)} />
					<ErrorMessage errorObj={errors.masterName}/>

					<Input type="tel" label="동물등록번호 15자리" placeholder="선택" 
						aria-invalid={errors.registerNo}
						register={register('registerNo',registerOptions.registerNo)} />
					<ErrorMessage errorObj={errors.registerNo}/>

				</section>

				<section>
					<h4 className="title">펫보험 가입여부</h4>
					<RadioGroup>
						<RadioItem name="isPetInsurance" value="Y"
							register={register('isPetInsurance', registerOptions.isPetInsurance)} > 없어요 </RadioItem>
						<RadioItem name="isPetInsurance" value="N"							
							register={register('isPetInsurance', registerOptions.isPetInsurance)} > 없어요 </RadioItem>
					</RadioGroup>
				</section>

				<StyeldSubmitButton isValid={isValid}>입력완료</StyeldSubmitButton>

			</form>
		</StyledPageLayout>
	)
}


// (/^\d{4}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[01])$/);
